@font-face {
    font-family: 'Source Sans Pro';
    src: url('./fonts/source-sans-pro/SourceSansPro-Regular.otf') format('opentype');
    font-weight: normal;
    font-style: normal;
    line-height: 1.5;
  }

.emoijContainer {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    flex-direction: row;
    margin-bottom: 10px;
    border: 1px solid #555867;
    border-radius: 20px;
    margin-bottom: 20px;
}

.emojiButton {
    transition: all 200ms cubic-bezier(0.2, 0.8, 0.4, 1);
    margin: 2px 0px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 23px;
    height: 23px;
    padding: 3px;
    border: 1px solid transparent;
    border-radius: 20px;
    user-select: none;
    cursor: pointer;
    &:first-child {
        margin-left: 2px;
    }
    &:last-child {
        margin-right: 2px;
    }
    &:hover {
        border: 1px solid #555867;
    }
    &:active {
        box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2) inset;
    }
}

textarea {
    transition: all 200ms cubic-bezier(0.2, 0.8, 0.4, 1);
    width: 90%;
    max-height: 60px;
    resize: none;
    background-color: #f0f2f6;
    border-radius: 0.5em;
    display: flex;
    padding: 8px;
    border: 1px solid transparent;
    line-height: 21px;
    font-size: 16px;
    outline: none;
    font-family: "Source Sans Pro", sans-serif;
    &:focus {
        border: 1px solid salmon;
    }
}

textarea:focus + .messageBlock > svg {
    /* some sweet CSS */
    color: #ff4b4b;
}

textarea::-webkit-scrollbar,
textarea::-webkit-scrollbar-button,
textarea::-webkit-scrollbar-track,
textarea::-webkit-scrollbar-track-piece,
textarea::-webkit-scrollbar-thumb,
textarea::-webkit-scrollbar-corner,
textarea::-webkit-resizer {
	background: none;
}

textarea::-webkit-scrollbar {
	width:  7px;
	height: 7px;
}
textarea::-webkit-scrollbar-track-piece {
    margin-top: 2px;
  	background: transparent;
}
textarea::-webkit-scrollbar-thumb {
	background:#ff4b4b;
    border-radius: 0.5em;
}
textarea::-webkit-scrollbar-button {
	width:0;
	height:0;
}

.sendButton {
    display: flex;
    cursor: pointer;
}
.messageBlock {
    display: flex;
    gap: 7px;
    flex-direction: row;
    align-items: flex-end;
    justify-content: center;
}
.sendButtonSVG {
    padding: 7px;
    height: 24px;
    color: #555867;
    cursor: pointer;
}

.sendButtonSVG_hover:hover {
    transition: background-color 200ms cubic-bezier(0.2, 0.8, 0.4, 1);
    border-radius: 0.5rem;
    background-color: #ff4b4b;
    fill: #fff;
    &:active {
        box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2) inset;
    }
}

.bodyContent {
    display: flex;
    flex-direction: column;
    height: 175px;
    gap: 12px;
    justify-content: flex-end;
}

@media (max-width: 540px) {
    .emoijContainer {
      display: none;
    }
    .messageBlock {
      margin-bottom: 10px;
    }
    .bodyContent {
        height: 90px;
    }
  }